/* eslint-disable no-unused-vars */
import React from 'react';
import {Link} from 'react-router-dom'
import RoomIcon from '@material-ui/icons/Room';
import ClearIcon from '@material-ui/icons/Clear';
import EmailIcon from '@material-ui/icons/Email';
import Email from '@material-ui/icons/Email';
import PhoneIcon from '@material-ui/icons/Phone';

const MobileNav = () => {
  function  closeMobileNav(){
        const mobileNav = document.getElementById('mobileNav');
        mobileNav.style.display="none"
    }
    return (
        <div className="mobil_nav" id="mobileNav">
            <div className="header">
                <div className="change_lang">
                <a href="#" className="active">UZ</a>
                <a href="#">RU</a>
                </div>
                <div className="location">
                    <RoomIcon />
                    <p>Tashkent</p>
                </div>
                <div className="close" id="closeMobileNav" onClick={closeMobileNav}>
                    <ClearIcon />
                </div>
            </div>
            <div className="main">
                <ul>
                    <li><Link to="/service">Xizmatlar</Link></li>
                    <li><Link to="/project2">Portfolio</Link></li>
                    <li><Link to="/service">Yangiliklar</Link></li>
                    <li><Link to="/service">Blog</Link></li>
                    <li><Link to="/service">Firma xaqida</Link></li>
                    <li><Link to="/service">Kontaktlar</Link></li>
                </ul>
            </div>
        </div>
    );
};


export default MobileNav;