/* eslint-disable no-unused-vars */
import React from 'react';
import clever1 from '../images/clever1.png'

import AppStore from '../images/AppStore.jpg'
import GooglePlay from '../images/GooglePlay.jpg'
import cleverlogo from '../images/cleverlogo.png'

const MainProject2 = () => {
    return (
        <div className="mainproject2">
            <div className="parent">
                <div className="main1">
                    <h1>Mobile application for Clever Park residential area</h1>
                    <h4>Technology of the future</h4>
                    <div className="downapp">
                        <a href="#" ><img src={AppStore} /> </a>
                        <a href="#" ><img src={GooglePlay} /> </a>
                    </div>
                </div>
                <div className="main2">
                    <img src={clever1} />
                </div>
            <div className="cleverlogo">
                <img src={cleverlogo} />
            </div>
        </div>
        </div>
    );
};


export default MainProject2;