import React from 'react';
import mobilegif from '../images/mobilegif.gif'
const AboutService = () => {
    return (
        <div className="aboutservice">
            <div className="a_gif">
                <img src={mobilegif} />
            </div>
            <div className="gif_about">
                <h3>Business mobile dasturi har doimgidek eng oson yo'ldir
                      mijozlar, sheriklar yoki xodimlarga yaqin</h3>
                <p>Biz iOS va Android platformalari uchun mobil biznes dasturlarini ishlab chiqishga ixtisoslashganmiz.
                    Biz konstruktorlardan yoki shablonlardan foydalanmaymiz, har bir ishlab chiqarish sizning korxonangiz uchun
                     maxsus ishlab chiqilgan funksionallik va dizayn jihatidan noyob mahsulotdir. Biz sizning mijozlaringiz va mijozlaringiz 
                     uchun ikkala dasturni, shuningdek xodimlar
                     uchun korporativ dasturlarni ishlab chiqamiz, korxonada allaqachon ishlayotgan biznes jarayonlari 
                     va buxgalteriya tizimlari bilan integratsiyalashamiz.
                </p>
                
            </div>
        </div>
    );
};


export default AboutService;