/* eslint-disable jsx-a11y/alt-text */
import React from 'react';
import OwlCarousel from 'react-owl-carousel'
import 'owl.carousel/dist/assets/owl.theme.default.min.css'
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import img1 from '../images/banner_1.png'
import img2 from '../images/banner_3.png'
import img3 from '../images/banner_5.jpg'


const HomeCarousel = () => {
    return (
        <>
        <div className="homeCarusel">
            <div className="caruselText">
                <h1>
                 Kompleks IT-yechim <span id="changeText">banklar </span>
                 uchun
                </h1>
                <button className="podrobne">
                    Batafsil
                </button>
            </div>
            <div className="carusel">
            <OwlCarousel
            className="owl-theme" 
            items="3"
            autoplay="true"
            autoWidth="true"
            dots="3"
            lazyLoad="true"
            freeDrag="true"
            center="true"
            >
                <div className="item">
                <img src={img1} />
                </div>
                <div className="item">
                <img src={img2} />
                </div>
                <div className="item">
                <img src={img3}  />
                </div>
            </OwlCarousel>
            </div>
        </div>
        </>
    );
};

export default HomeCarousel;