/* eslint-disable no-unused-vars */
import React, { useEffect } from 'react';
import {Container , Col , Row} from 'reactstrap'
import Aos from 'aos'
import 'aos/dist/aos.css'
import smartlogo from '../images/smart_logo.png'
import smartdevices from '../images/smart_devices.png'




const Solutions = () => {
    useEffect(() =>{
        Aos.init({duration : 500});
    } , []) 
    return (
        <div >
            <div className="smartTitle">
                <h2>Mijozlar uchun smart yechimlar</h2>
                <hr className="afterhr"></hr>
            </div>
        <div className="solutions1">
        <div className="smartlogo">
            <img src={smartlogo} />
        </div>
        <div className="smarttext">
            <h2>Platforma <span>Aqlli Xonadon</span></h2>
             <p>Yangi uy-joylarni boshqarishning eng samarali usuli</p>
        </div>
        <div className="smartdevices">
            <img src={smartdevices} />
        </div>
        </div>
        <div className="example1">
            <button>
                Smartbolla.uz
            </button>
        </div>
        </div>
    );
};


export default Solutions;