/* eslint-disable jsx-a11y/anchor-is-valid */
import React , { useState }  from 'react';
import { ButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
const NavHeader = () => {
    const [dropdownOpen, setOpen] = useState(false);

    const toggle = () => setOpen(!dropdownOpen);

    return (
        <>
        <div className="nav_header">
            <nav>
                <ul>
                    <li className="services">
                        <ButtonDropdown className="servicesBtn"  isOpen={ dropdownOpen } toggle={ toggle }>
                        <DropdownToggle className="servicesBtnn" caret>
                        Xizmatlar
                        </DropdownToggle>
                        <DropdownMenu className="dropdownmenu">
                            <DropdownItem className="DropdownItem"><a href="/service">Veb dasturlash</a></DropdownItem>
                            <DropdownItem className="DropdownItem"><a href="/service">Mobil ilovalar</a></DropdownItem>
                            <DropdownItem className="DropdownItem"><a href="/service">Avtomatlashtirish</a></DropdownItem>
                            <DropdownItem className="DropdownItem"><a href="/service">SEO optimizatsiya</a></DropdownItem>
                        </DropdownMenu>
                        </ButtonDropdown>
                    </li>
                    <li><a href="/project2" >Portfolio</a></li>
                    <li><a href="#" >Yangiliklar</a></li>
                    <li><a href="#" >Blog</a></li>
                    <li><a href="#" >Firma xaqida</a></li>
                    <li><a href="#footer" >Kontaktlar</a></li>
                    
                </ul>
            </nav>
        </div>
        </>
    );
};


export default NavHeader;