import React from 'react';
import { Link } from 'react-router-dom'
import logo from '../images/logo.gif'
import RoomIcon from '@material-ui/icons/Room';
import EmailIcon from '@material-ui/icons/Email';
import TelegramIcon from '@material-ui/icons/Telegram';
import PhoneIcon from '@material-ui/icons/Phone';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';

const Footer = () => {
    return (
        <footer id="footer">
            <div className="firmlogo">
                <a href="wellsoft.pro"><img src={logo} /></a>
            </div>
            <div className="wrapnav">
                <div className="location">
                    <RoomIcon />
                    <p>Tashkent, Integro B block</p>
                </div>
                <div className="mail">
                    <EmailIcon />
                    <p>hello@wellsoft.pro</p>
                </div>
                <div className="tell">
                    <PhoneIcon />
                    <p>+998 33 290 70 80</p>
                </div>
            </div>
            <div className="otherlinks">
                <div className="company">
                    <p>Soft+</p>
                    <span>Kutilganidan yuqori bo'lgan dasturiy yechimlar</span>
                </div>
                <div className="services">
                    <p>Xizmatlar</p>
                   <p> <Link to="/service">Veb dasturlash</Link></p>
                   <p> <Link to="/service">Kompleks reklama</Link></p>
                   <p> <Link to="/service">Mobil ilovalar</Link></p>
                   <p> <Link to="/service">Avtomatlashtirish</Link></p>
                </div>
                <div className="solutions">
                <p>Sanoat yechimlari</p>
                <p><Link to="/pageNotFound">Quruvchilar</Link></p>
                <p><Link to="/pageNotFound">Boshqaruv kompaniyalar</Link></p>
                <p><Link to="/pageNotFound">Transport kompaniyalar</Link></p>
                <p><Link to="/pageNotFound">Banklar uchun</Link></p>
                
                </div>
                <div className="alllink">
                    <Link to="/project2">Portfolio</Link>
                    <Link to="/project2">Blog</Link>
                    <Link to="/project2">Fikrlar</Link>
                    <Link to="/project2">Kontaktlar</Link>
                </div>
            </div>
                <div className="socials">
                    <div className="telegram">
                    <TelegramIcon />
                    <p>/wellsoft</p>
                    </div>
                    <div className="facebook">
                    <FacebookIcon />
                    <p>/wellsoftPro</p>
                    </div>
                    <div className="instagram">
                    <InstagramIcon />
                    <p>/itwellsoft</p>
                    </div>
                </div>
        </footer>
    );
};


export default Footer;