import React , {useEffect} from 'react';
import {Row , Col , Container} from 'reactstrap'
import { Link } from 'react-router-dom'
import Aos from 'aos'
import 'aos/dist/aos.css'
import app1 from '../images/app.webp'

const Services = () => {
    useEffect(() =>{
        Aos.init({duration : 500});
    } , []) 
    return (
        <>
        <div className="services" >
            <Container>
            <div className="servicest">
                <h2>Xizmatlar</h2>
                <hr></hr>
            </div>
                <Row>
                    <Col md="6" className="wrap">
                        <Link to="/service">
                        <div className="mobil">
                            <p>Mobil iovalar</p>
                        </div>
                        <div className="parent">
                            <div className="child">
                                <p>"      iOS  Android     ,        "</p>
                            </div>
                        </div>
                        </Link>
                    </Col>

                    <Col md="6" className="wrap2">
                        <Link to="/service">
                        <div className="avtomatlashtirish">
                            <p>Tizimni avtomatlashtirish</p>
                        </div>
                        <div className="parent2">
                            <div className="child2">
                                <p>                       </p>
                            </div>
                        </div>
                        </Link>
                    </Col>
                </Row>
                <Row className="row2">
                    <Col md="6" className="kompleks">
                    <Link to="/service">
                        <div className="komplekst">
                            <p>Kompleks Targ'ibot</p>
                        </div>
                        <div className="parent3">
                            <div className="child3">
                                <p>"      iOS  Android     ,        "</p>
                            </div>
                        </div>
                        </Link>
                    </Col>
                    <Col md="6" className="razrabotka">
                    <Link to="/service">
                        <div className="razrabotkat">
                            <p>Veb dasturlash</p>
                        </div>
                        <div className="parent4">
                            <div className="child4">
                                <p>"      iOS  Android     ,        "</p>
                            </div>
                        </div>
                        </Link>
                    </Col>
                </Row>
            <div className="allservicesbtn">
            <Link to="/service">
            <button >
            Barcha xizmatlar
            </button>
            </Link>
            </div>
            </Container>

        </div>
        </>
    );
};


export default Services;