import React from 'react';
import {Container} from 'reactstrap'
import RoomIcon from '@material-ui/icons/Room';
import EmailIcon from '@material-ui/icons/Email';
import Email from '@material-ui/icons/Email';
import logo from '../images/logo.gif'
import PhoneIcon from '@material-ui/icons/Phone';
import MenuIcon from '@material-ui/icons/Menu';




const Navigation = () => {
 
    const openMobileNav  = () =>{
        const navigation = document.getElementById('navigation');
        const mobileNav = document.getElementById('mobileNav');
        mobileNav.style.display="block";
        navigation.style.display = "none";
    }
    
 

        

    return (
        <>
        <header id="navigation">
            <div className="logo" id="logo">
                <a href="/" ><img src={logo} /></a>
            </div>
            <div className="openMobilMenu" id="openMobileMenu" onClick={openMobileNav} >
                <MenuIcon />
            </div>
            <div className="mail">
            <EmailIcon />
            <p>example.com@gmail.com</p>
            </div>
            <div className="tell">
                <PhoneIcon />
                <a href="tel: +998 (90) 806-20-04">
                    +998 90 806 20 04
                </a>
            </div>
            <div className="call_back">
                <span >Qayta qo'ng'iroq</span>
            </div>
            <div className="change_lang">
                <a href="#" className="active">UZ</a>
                <a href="#">RU</a>
            </div>
        </header>
        </>
    );
    }


export default Navigation;